import React from 'react';
import '../../styles/program.scss';
import ViennaGallery from './ViennaGallery';

const Vienna = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Muzyczny Wiedeń</h1>
                    <h4>Ktoś przez radio mówi mi, że dziś w Wiedniu deszcz i mgły.
                    Skąd to wzruszenie i dreszcz, Wiedeń i ty, i deszcz.
                    Naddunajski mokry bruk, kroków twoich znany stuk,
                        blade latarnie na gaz, a w tle zapewne walc.<span>Andrzej Waligórski</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <ViennaGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzień I</h5>
                            <p>Zbiórka. Wyjazd w kierunku Wiednia o godzinie…. Przyjazd do hotelu w okolicach Wiednia. Zakwaterowanie. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzień II</h5>
                            <p>Rano po śniadaniu wyruszymy na zwiedzanie stolicy Austrii. Dzień rozpoczniemy od fundamentów, które pozwolą nam później na zrozumienie tego pięknego miasta- czyli przejazdem reprezentacyjną aleją miasta Ringstrasse, przy której ulokowane są najpiękniejsze budowle w mieście: Urania, Muzeum Sztuki Użytkowej, Park Miejski, Opera, plac Marii Teresy, Hofburg, Parlament, Ratusz, Teatr Zamkowy, Uniwersytet, Kościół Wotywny, budynek Giełdy, Pawilony Otto Wagnera, największy kościół barokowy Karola Boromeusza, Plac Karola z Towarzystwem Muzycznym, gdzie odbywa się Koncert Noworoczny itd. Przejedziemy również w stronę słynnego Prateru i zobaczymy najstarsze w Europie wesołe miasteczko wraz z Diabelskim Młynem. Następnie udamy się na Stare Miasto i zobaczymy jego główną część. Geograficzny i duchowny środek Wiednia wyznacza strzelista wieża katedry św. Szczepana oraz Plac św. Szczepana. Świątynia jest arcydziełem gotyku, uchodzi za jedną z najpiękniejszych europejskich katedr. Od ponad 800 lat bije w niej religijne serce miasta. Otoczenie, w którym się znajduje jest kwintesencją ducha Wiednia, gdzie nowoczesność przeplata się z historią. W niedalekiej odległości znajduje się słynny Kolhmarkt – „targ węglowy”, będący siedzibą najbardziej ostentacyjnych, luksusowych domów mody. Wizyta w Hofburgu, którego zabudowania obejmują dawną miejską rezydencję Habsburgów, kościół, kaplicę, Hiszpańską Szkołę Jazdy (z zewnątrz), muzea i Austriacką Bibliotekę Narodową (z zewnątrz). Wstąpimy do dawnych apartamentów cesarskich, Muzeum Sisi oraz Silberkammer, gdzie znajduje się unikalna kolekcja cesarskich sreber. Czas wolny na odpoczynek i kawę. Po dniu pełnym wrażeń przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzień III</h5>
                            <p>Śniadanie. Ten dzień poświęcimy na zwiedzanie Schönburnn- dawnej letniej rezydencji rodziny cesarskiej, która wzięła swoją nazwę od „pięknego źródła”, które odkryto tam w XVII w. Wystrój każdej komnaty zapiera dech rokokowym przepychem, misternym, kolorystycznym bogactwem dekoracji malarskiej i gąszczem złoconych liści. Do cudów, jakie można tam obejrzeć należą m.in.: Okrągły gabinet chiński, Wielka Galeria, Ukryta klatka schodowa, Niebieski salon Chiński. Budowla robi niesamowite wrażenie ale uzupełnia je wizyta w przepięknym ogrodzie, który rozciąga się na tyłach pałacu i jest absolutnie bezkonkurencyjny we Wiedniu. Na 65 ha powierzchni znajduje się barokowy ogród, obsadzony drzewami park, zwierzyniec, palmiarnia, kilka pawilonów, grot i architektonicznych wymysłów. Ponadto fontanny, pomniki stojące wśród drzew i alejek. Następnie przejazd do Wiedeńskiego Muzeum Historii Sztuki, gdzie zgromadzona została jedna z największych i najwspanialszych kolekcji dzieł sztuki na świecie, do których zaliczamy m.in.: największy zbór dzieł Rubensa, bogatą kolekcję prac Pietera Brueghla Starszego. Ponadto znajdziemy tam płótna takich artystów jak Rembrant, Tycjan, Caravaggio itd. Galeria obejmuje ogromną kolekcję przez lata gromadzoną przez Habsburgów, rozlokowaną w ponad stu pomieszczeniach na trzech poziomach. Po takiej dawce doznań artystyczno- historycznych relaksujący spacer po ogrodach Burgartten, które założone zostały przez Habsburgów w centrum miasta na terenach otaczających Hofburg. Spacerując wśród drzew, mija się pomniki Goethego, Mozarta, cesarza Franciszka. Park słynie również z secesyjnych oranżerii zaprojektowanych przez Friedricha Ohmanna. W jednej z nich urządzono „Dom Motyli”, gdzie w sztucznie stworzonych warunkach lasu tropikalnego żyje ponad 150 gatunków tych owadów. Po całodziennym zwiedzaniu udamy się do hotelu na obiadokolację i nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzień IV</h5>
                            <p>Śniadanie. Tego dnia pojedziemy zobaczyć Belweder (z zewnątrz);  Hundertwasserhaus – „bajkowy dom”, którego dachy porośnięte są dziesiątkami drzew i krzewów tworząc urocze ogrody (z zewnątrz). Następnie ponownie udamy się do zabytkowego i historycznego centrum Wiednia, tym razem na spacer do dzielnicy żydowskiej gdzie zobaczymy zabytkowe zabudowania żydowskiego Wiednia m.in. : Muzeum Żydowskie, Mauzoleum Holokaustu, Stadttempel – jedyna we Wiedniu synagoga, cudem ocalona od zagłady, stary ratusz - Altes Rathaus- z wyniosłą barokową fasadą; Następnie przejdziemy na Platz Am Hof otoczony XVI-XVII wieczną zabudową w którą wkomponowany jest m.in. pałac Collalto, w którym miało miejsce pierwsze publiczne wystąpienie sześcioletniego wówczas  W.A. Mozarta. Najważniejszą atrakcją placu jest kościół Am Hof (Dziewięciu Chórów Anielskich). Czas wolny na zakup pamiątek bądź wiedeńską kawę. Przejazd do hotelu. Obiadokolacja. Nocleg</p>
                        </div>

                        <div className="fifth-day">
                            <h5>Dzień IV</h5>
                            <p>Wykwaterowanie z hotelu. Po śniadaniu pojedziemy na wzgórze Kahlenberg, z której król Jan III Sobieski w 1683 roku dowodził zwycięską bitwą nad Turkami. Znajduje się tam kościółek, w którym król Polski modlił się przed bitwą oraz tablica poświęcona tym wydarzeniom. Oprócz tego ze wzgórza roztacza się przepiękna panorama na Wiedeń. Po ostatnim spojrzeniu na stolicę Austrii wyruszymy w drogę powrotną do Polski.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Vienna;